import { Flex } from '@pancakeswap/uikit'
import React from 'react'
import { Farm } from 'state/types'

interface Props {
  farm: Farm
  size?: 'sm' | 'lg'
}

const FarmImage: React.FC<Props> = ({ farm, size }) => {
  const symbolLower = farm.token.symbol.toLocaleLowerCase()
  const quoteSymbolLower = farm.quoteToken.symbol.toLowerCase()
  let imgSize
  if (farm.isTokenOnly) {
    imgSize = size === 'sm' ? 48 : 64
  } else {
    imgSize = size === 'sm' ? 32 : 48
  }
  return farm?.isTokenOnly ? (
    <img
      src={`/images/farms/${symbolLower}.png`}
      alt={symbolLower}
      width={imgSize}
      height={imgSize}
      style={{ height: imgSize }}
    />
  ) : (
    <Flex className="flex space-x-1">
      <img
        src={`/images/farms/${symbolLower}.png`}
        alt={symbolLower}
        width={imgSize}
        height={imgSize}
        style={{ height: imgSize }}
      />
      <img
        src={`/images/farms/${quoteSymbolLower}.png`}
        alt={quoteSymbolLower}
        width={imgSize}
        height={imgSize}
        style={{ marginLeft: size === 'sm' ? '-12px' : '-18px', height: imgSize }}
      />
    </Flex>
  )
}

export default FarmImage
